/* Variables */
.toast-title {
  font-weight: bold; }

.toast-message {
  -ms-word-wrap: break-word;
  word-wrap: break-word; }
  .toast-message a, .toast-message label {
    color: #ffffff; }
  .toast-message a:hover {
    color: #cccccc;
    text-decoration: none; }

.toast-close-button {
  position: relative;
  right: -0.3em;
  top: -0.3em;
  float: right;
  font-size: 20px;
  font-weight: bold;
  color: #ffffff;
  -webkit-text-shadow: 0 1px 0 #ffffff;
  text-shadow: 0 1px 0 #ffffff;
  opacity: 0.8;
  -ms-filter: progid:DXImageTransform.Microsoft.Alpha(Opacity=80);
  filter: alpha(opacity=80); }
  .toast-close-button:hover, .toast-close-button:focus {
    color: #000000;
    text-decoration: none;
    cursor: pointer;
    opacity: 0.4;
    -ms-filter: progid:DXImageTransform.Microsoft.Alpha(Opacity=40);
    filter: alpha(opacity=40); }

/*Additional properties for button version
 iOS requires the button element instead of an anchor tag.
 If you want the anchor version, it requires `href="#"`.*/
button.toast-close-button {
  padding: 0;
  cursor: pointer;
  background: transparent;
  border: 0;
  -webkit-appearance: none; }

.toast-top-full-width {
  top: 0;
  right: 0;
  width: 100%; }

.toast-bottom-full-width {
  bottom: 0;
  right: 0;
  width: 100%; }

.toast-top-left {
  top: 12px;
  left: 12px; }

.toast-top-center {
  top: 12px; }

.toast-top-right {
  top: 12px;
  right: 12px; }

.toast-bottom-right {
  right: 12px;
  bottom: 12px; }

.toast-bottom-center {
  bottom: 12px; }

.toast-bottom-left {
  bottom: 12px;
  left: 12px; }

.toast-center {
  top: 45%; }

#toast-container {
  position: fixed;
  z-index: 999999;
  /*overrides*/ }
  #toast-container.toast-center, #toast-container.toast-top-center, #toast-container.toast-bottom-center {
    width: 100%;
    pointer-events: none; }
  #toast-container.toast-center > div, #toast-container.toast-top-center > div, #toast-container.toast-bottom-center > div {
    margin: auto;
    pointer-events: auto; }
  #toast-container.toast-center > button, #toast-container.toast-top-center > button, #toast-container.toast-bottom-center > button {
    pointer-events: auto; }
  #toast-container * {
    -moz-box-sizing: border-box;
    -webkit-box-sizing: border-box;
    box-sizing: border-box; }
  #toast-container > div {
    margin: 0 0 6px;
    padding: 15px 15px 15px 50px;
    width: 300px;
    -moz-border-radius: 3px 3px 3px 3px;
    -webkit-border-radius: 3px 3px 3px 3px;
    border-radius: 3px 3px 3px 3px;
    background-position: 15px center;
    background-repeat: no-repeat;
    -moz-box-shadow: 0 0 12px #999999;
    -webkit-box-shadow: 0 0 12px #999999;
    box-shadow: 0 0 12px #999999;
    color: #ffffff;
    opacity: 0.8;
    -ms-filter: progid:DXImageTransform.Microsoft.Alpha(Opacity=80);
    filter: alpha(opacity=80); }
  #toast-container > :hover {
    -moz-box-shadow: 0 0 12px #000000;
    -webkit-box-shadow: 0 0 12px #000000;
    box-shadow: 0 0 12px #000000;
    opacity: 1;
    -ms-filter: progid:DXImageTransform.Microsoft.Alpha(Opacity=100);
    filter: alpha(opacity=100);
    cursor: pointer; }
  #toast-container > .toast-info {
    background-image: url("data:image/png;base64,") !important; }
  #toast-container > .toast-wait {
    background-image: url("data:image/gif;base64,") !important; }
  #toast-container > .toast-error {
    background-image: url("data:image/png;base64,") !important; }
  #toast-container > .toast-success {
    background-image: url("data:image/png;base64,") !important; }
  #toast-container > .toast-warning {
    background-image: url("data:image/png;base64,") !important; }
  #toast-container.toast-top-full-width > div, #toast-container.toast-bottom-full-width > div {
    width: 96%;
    margin: auto; }

.toast {
  background-color: #030303; }

.toast-success {
  background-color: #51a351; }

.toast-error {
  background-color: #bd362f; }

.toast-info, .toast-wait {
  background-color: #2f96b4; }

.toast-warning {
  background-color: #f89406; }

/*Responsive Design*/
@media all and (max-width: 240px) {
  #toast-container > div {
    padding: 8px 8px 8px 50px;
    width: 11em; }
  #toast-container .toast-close-button {
    right: -0.2em;
    top: -0.2em; } }

@media all and (min-width: 241px) and (max-width: 480px) {
  #toast-container > div {
    padding: 8px 8px 8px 50px;
    width: 18em; }
  #toast-container .toast-close-button {
    right: -0.2em;
    top: -0.2em; } }

@media all and (min-width: 481px) and (max-width: 768px) {
  #toast-container > div {
    padding: 15px 15px 15px 50px;
    width: 25em; } }

/*
 * AngularJS-Toaster
 * Version 0.3
*/
:not(.no-enter)#toast-container > div.ng-enter, :not(.no-leave)#toast-container > div.ng-leave {
  -webkit-transition: 1000ms cubic-bezier(0.25, 0.25, 0.75, 0.75) all;
  -moz-transition: 1000ms cubic-bezier(0.25, 0.25, 0.75, 0.75) all;
  -ms-transition: 1000ms cubic-bezier(0.25, 0.25, 0.75, 0.75) all;
  -o-transition: 1000ms cubic-bezier(0.25, 0.25, 0.75, 0.75) all;
  transition: 1000ms cubic-bezier(0.25, 0.25, 0.75, 0.75) all; }

:not(.no-enter)#toast-container > div.ng-enter.ng-enter-active {
  opacity: 0.8; }

:not(.no-leave)#toast-container > div.ng-leave {
  opacity: 0.8; }
  :not(.no-leave)#toast-container > div.ng-leave.ng-leave-active {
    opacity: 0; }

:not(.no-enter)#toast-container > div.ng-enter {
  opacity: 0; }
